@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body,html{width:100vw;height:100vh;background:#fdf1e5;overflow-x:hidden}h1{color:#5b6cb9}h4{margin:40px}li{list-style:none;margin:0;padding:0}a{text-decoration:none}a:hover{cursor:pointer;text-decoration:underline;-webkit-text-decoration-color:#5f9ea0;text-decoration-color:#5f9ea0;-webkit-transform:translateY(-10%);transform:translateY(-10%)}.loading-ring{display:inline-block;width:80px;height:80px;margin:0 auto}.loading-ring:after{content:" ";display:block;width:64px;height:64px;margin:0 auto;margin:8px;border-radius:50%;border:6px solid rgba(224,96,96,0.883);border-color:rgba(224,96,96,0.883) transparent rgba(224,96,96,0.883) transparent;-webkit-animation:loading-ring 1.2s linear infinite;animation:loading-ring 1.2s linear infinite}@-webkit-keyframes loading-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes loading-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loading-ripple{display:inline-block;position:relative;width:80px;height:80px}.loading-ripple div{position:absolute;border:4px solid #cef;opacity:1;border-radius:50%;-webkit-animation:loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:loading-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite}.loading-ripple div:nth-child(2){-webkit-animation-delay:-0.5s;animation-delay:-0.5s}@-webkit-keyframes loading-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}@keyframes loading-ripple{0%{top:36px;left:36px;width:0;height:0;opacity:1}100%{top:0px;left:0px;width:72px;height:72px;opacity:0}}button{background-color:#dafdf1;padding:5px;font-size:19px;border-radius:5px;border:1px solid #5f9ea0;color:#3f51b5;outline:none}button:hover{cursor:pointer;background-color:#e6e6fa;-webkit-transform:translateY(-10%);transform:translateY(-10%);box-shadow:4px 4px 8px 0px rgba(34,60,80,0.2)}.hidden{display:none}@media (max-width: 960px){.main-container{width:95%}}@media (max-width: 769px){table{min-width:500px;font-size:0.9rem}th{font-size:0.9rem}}@media (max-width: 500px){table{font-size:0.8rem;min-width:-webkit-max-content;min-width:max-content}th{font-size:0.75rem;padding:2px}td{width:5rem}td:first-child{width:-webkit-max-content;width:max-content;font-size:0.6rem}}@media (max-width: 425px){table{font-size:0.5rem}th{font-size:0.5rem}}

.header{width:90%;max-width:1400px;margin:0 auto}.header h4{font-size:21px;color:#5f9ea0;letter-spacing:1.4px}.header-authorization{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end}.header-authorization button{padding:7px;border-radius:5px}.header-cart{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:1rem}.to-main-button{background-color:inherit;border:none;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;outline:none}.cart-button{background-color:inherit;border:none;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;outline:none;font-size:1.5rem}.shopping-cart{width:30px;margin:10px}

.login-page-wrapper{padding-top:20vh;height:100vh}.login-page-wrapper h2{font-size:30px;color:#5b6cb9}.login-page{height:60vh;margin:auto;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}#authorization{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;height:30%}#authorization label{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;line-height:47px;width:100%}#authorization input{width:100%;padding:5px}#authorization button{width:100%;font-weight:bold;border-radius:0;border-radius:initial}.name-error{text-align:center;font-size:23px;color:lightcoral}

.book-card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:320px;height:550px;margin:15px;padding:20px;border:1px solid cadetblue}.book-card:hover{box-shadow:6px 6px 10px 0px rgba(34,60,80,0.2)}.book-card_image-container{height:67%;width:99%;margin:0 auto;text-align:center}.book-card_image-container img{height:100%;max-width:95%}.book-card_title{line-height:1.7rem;font-size:1.4rem}.book-card_subtitle{margin-top:0.9rem;line-height:1.4rem;font-size:1.1rem}.book-card-footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;width:90%}.book-card_price{font-weight:bold;font-size:1.2rem;color:#3f51b5}.book-card_button{padding:0.5rem 1.6rem}.book-card_price{font-weight:bold;font-size:1.2rem;color:#3f51b5}

.main-controls{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin:40px}.book-seach-lable img{position:relative;top:3.4px;left:71px;width:1.2rem}#book-seach{margin:10px 40px;min-width:19rem;height:2rem;padding:1.1rem;padding-left:2.2rem;font-size:1.4rem;border:1px solid #5f9ea0;border-radius:2rem;outline:none}.book-list{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-flex-wrap:wrap;flex-wrap:wrap}#price-filter{border:1px solid #5f9ea0;min-width:19rem;padding:0.3rem;font-size:1.2rem;margin:10px 60px;outline:none}

table{border-collapse:collapse;line-height:1.1;font-family:"Lucida Sans Unicode", "Lucida Grande", sans-serif;color:#0C213B;margin:2rem auto;max-width:1200px;width:95vw}th{border:2px solid rgba(226,221,221,0.911);background-color:whitesmoke;height:2rem;text-align:start;padding:0.6rem}td{border:2px solid rgba(226,221,221,0.911);height:2rem;padding:0.6rem}td:first-child{width:500px;color:#2d4444}td:nth-child(2),td:nth-child(3){width:50px}td:last-child{width:100px}tbody tr:nth-child(even){background-color:rgba(226,221,221,0.911)}.cart-cart_total-price{text-align:end;font-size:1.4rem;font-weight:600}

.purchase-container{position:absolute;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;top:0;left:0;width:100vw;height:100vh;background-color:rgba(128,128,128,0.527)}.purchase{min-height:400px;background-color:#fdf1e5;padding:20px;border-radius:10px}.purchase p{font-size:1.5rem;padding:1rem}.purchase-controls{display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;padding:2rem}#purchase_close-button{background:none;border:none;outline:none;text-decoration:underline}#purchase_close-button:hover{box-shadow:none}

.cart-container{width:100vw;max-width:1280px;margin:0 auto}.cart{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-end;align-items:flex-end;padding:20px}.empty-cart{width:95%;height:calc(100vh - 210px);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:24px;font-weight:bold}.empty-cart img{width:55px;margin:20px}.cart_purchase-button:disabled{background-color:rgba(226,221,221,0.911);cursor:not-allowed}

.book-details-container{display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}.book-details{display:-webkit-flex;display:flex}.book-details-content{padding:20px}.book-details_image-container{width:400px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:10px}.book-details_image-container img{width:350px;margin:20px}.book-details_tags-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:10px}.book-details_tags-container img{width:30px;margin:10px 15px}.book-details-cart{width:330px;margin:30px;padding:10px;border:1px solid #5f9ea0}.book-details-cart_price,.book-count-label,.book-details-cart_total-price,.book-details-cart_footer{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;padding:10px 0}#book-count{min-width:50px}.book-details_title{line-height:2rem;font-size:1.4rem}.book-details_subtitle{line-height:2rem;font-size:1.3rem}.book-details_description{padding:1rem;font-size:1.2rem;letter-spacing:0.09rem}.book-details-cart_footer{-webkit-justify-content:flex-end;justify-content:flex-end}.book-details_cart-button{background-color:#dafdf1;text-decoration:none;padding:5px;font-size:19px;border-radius:5px;border:1px solid #5f9ea0;color:#3f51b5;outline:none;transition:0.2s ease-in-out}.book-details_cart-button:hover{cursor:pointer;text-decoration:none;background-color:#e6e6fa;-webkit-transform:translateY(-10%);transform:translateY(-10%);box-shadow:4px 4px 8px 0px rgba(34,60,80,0.2)}

.not-found-container{position:absolute;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;top:0;left:0;width:100vw;height:100vh;background-color:rgba(128,128,128,0.527)}.not-found{min-height:400px;min-width:60vw;background-color:#fdf1e5;padding:20px;border-radius:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly}#not-found_close-button{padding:1rem;border-radius:1rem;font-weight:bold}

